@import "../variables.scss";
@import "../mixins.scss";


* {
    margin : 0;
    padding :  0;
    box-sizing : border-box;
    font-family: "PT Sans",sans-serif;
}


a {
    text-decoration: none;
    color : inherit;                   
}

#content  {
    line-height: 1.3;
    min-height: 60vh;
    display: flex;
    flex-wrap : wrap;
    @include respond("mobile") {
        flex-direction: column;
    }


    .action-icon {
        margin-left : .5rem;
    }

    .item-link {
        cursor: pointer;
    }

    .container-left  {
        position: relative;
        flex : 1 1 100%;
        display: flex;
        flex-direction: column;
        flex-wrap : wrap;
        padding : 1rem 2rem;
        background-color:$base-color-pink ;
        color : white;
        margin: 1.5rem 2rem 2rem;
        border-radius: 10px;
        box-shadow: 0px 3px 10px rgba(36, 36, 36, 0.664);
    }

    .main-title {
        position: relative;
        margin-top : 2rem;
        margin-left : 2rem;
        flex :  0 0 100%;
        font-size : 2rem;
        color : $base-color-dark-pink;
    }

    .main-title::after {
        content : "";
        margin-top : .5rem;
        display: block;
        width: 14rem;
        background-color : $base-color-dark-pink;;
        height : 4px;
    }


    ul {
        list-style-type: none;

        li {
            color : white;

            button {
                color : inherit;
            }
        }
    }

    .main-profile {
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-start;
        align-items : center;
        text-align: center;
        color : white;
        font-weight: 600;
        font-size : 1rem;

        .greet-user {
            font-size : 1.5rem;
            flex :  0 0  100%;
            text-align: left;
            margin-top : 1rem;
            margin-right : 1rem;
            border-radius: 10px;
        }

        .email {
            padding : .4rem .9rem;
            background-color: #303030;
            margin-top : 1rem;
            margin-right : 1rem;
            border-radius: 10px;
            box-shadow: 0px 2px 7px rgba(36, 36, 36, 0.712);
        }

        .age {
            padding : .4rem .9rem;
            background-color: #303030;
            margin-top : 1rem;
            margin-right : 1rem;
            border-radius: 10px;
            box-shadow: 0px 2px 7px rgba(36, 36, 36, 0.712);
        }

        .balance {
            margin-top : 1rem;
            padding : .4rem .9rem;
            border-radius: 10px;
            background-color: #303030;
            box-shadow: 0px 2px 7px rgba(36, 36, 36,  0.425);
        }

        .favorite-tag-list {
            flex : 0 0 100%;
            margin-top: 1.5rem;
            display: flex;
            flex-wrap: wrap;
            align-items: flex-end;
            justify-content: flex-start;

            li {    
                margin-top : 1rem;

                a {
                    font-size : 1rem;
    
                    margin-right : 1rem;
                    align-self: flex-end;
                    padding : .4rem .9rem;
                    border-radius: 10px;
                    background-color : #278acc;
                    text-align: center;
                    box-shadow: 0px 2px 7px rgba(36, 36, 36,  0.425);
    
                }
            }


            .favorite-tags {
                margin-bottom: .5rem;
                font-size : 1.2rem;
                padding : 0;
                font-weight: 600;
                width: 100%;
                background-color: transparent;
                text-align: left;
                box-shadow: none;
            } 
        }
    }

    .action-profile {
        margin-top : 3rem;
        display: flex;
        flex-wrap : wrap;



        .btn-action {
            margin-right : 1rem;
            display : block;
            margin-bottom : 1.5rem;
            font-weight: 600;
            font-size : 1rem;
            padding : .4rem .9rem;
            border : none;
            outline : none;
            background-color : #25a14e;
            border-radius: 10px;
            box-shadow: 0px 2px 7px rgba(36, 36, 36, 0.425);
        }
    }
}